package com.binkes.kizito_portfolio.components


import androidx.compose.runtime.Composable
import com.binkes.kizito_portfolio.util.ResObject
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.margin
import com.varabyte.kobweb.compose.ui.modifiers.size
import com.varabyte.kobweb.silk.components.graphics.Image
import org.jetbrains.compose.web.css.px


@Composable
fun RatingBar(modifier: Modifier= Modifier){

    Row (modifier = modifier){

        repeat(5){

            Image(
                modifier = Modifier.size(16.px)
                    .margin(right = if (it != 4) 2.px else 0.px),
                src = ResObject.Icon.star,
                description = "Star Icon"
            )
        }

    }



}