package com.binkes.kizito_portfolio.sections


import androidx.compose.runtime.Composable
import com.binkes.kizito_portfolio.components.Header
import com.binkes.kizito_portfolio.components.SocialBar
import com.binkes.kizito_portfolio.models.Section
import com.binkes.kizito_portfolio.models.ThemeByKizito
import com.binkes.kizito_portfolio.styles.MainButtonStyle
import com.binkes.kizito_portfolio.styles.MainImageStyle
import com.binkes.kizito_portfolio.util.ConstantsObject
import com.binkes.kizito_portfolio.util.ConstantsObject.LOREM_IPSUM_SHORTEST
import com.binkes.kizito_portfolio.util.ResObject
import com.varabyte.kobweb.compose.css.*
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.framework.annotations.DelicateApi
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.components.layout.SimpleGrid
import com.varabyte.kobweb.silk.components.layout.numColumns
import com.varabyte.kobweb.silk.components.navigation.Link
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.style.toModifier
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import org.jetbrains.compose.web.css.Color
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Button
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text

@Composable
fun MainSection( onMenuClicked: () -> Unit){


    Box(modifier = Modifier
        .id(Section.Home.id)
        .maxWidth(ConstantsObject.SECTION_WIDTH.px),
        contentAlignment = Alignment.TopCenter
    ) {

        MainBackground()

        MainContent(
            onMenuClicked = {
                onMenuClicked()
            }
        )


    }


}



@Composable
fun MainBackground(){

    Image(
        modifier = Modifier
            .fillMaxSize()
            .objectFit(ObjectFit.Cover),
        src = ResObject.Image.background,
        description = "background image"
    )

}


@OptIn(DelicateApi::class)
@Composable
fun MainContent( onMenuClicked: () -> Unit){

    val breakpoint = rememberBreakpoint()

    Column(modifier = Modifier
        .fillMaxSize(),
        horizontalAlignment = Alignment.CenterHorizontally,
        verticalArrangement = Arrangement.SpaceBetween
    ) {


        Header(
            onMenuClicked = {
                onMenuClicked()
            }
        )



        Column(modifier = Modifier
            .fillMaxSize(),
            verticalArrangement = Arrangement.Bottom,
            horizontalAlignment = Alignment.CenterHorizontally
        ) {

            SimpleGrid(
                modifier = Modifier
                    .fillMaxWidth(if (breakpoint >= Breakpoint.MD) 80.percent else 90.percent),
                numColumns = numColumns(base = 1, md = 2)
            ){

                MainText(breakpoint = breakpoint)

                MainImage()

            }

        }

    }
}




@Composable
fun MainText(breakpoint: Breakpoint){

    Row (
        horizontalArrangement = Arrangement.Center,
        verticalAlignment = Alignment.CenterVertically
    ){

        if (breakpoint >Breakpoint.MD) {
            SocialBar()
        }

        Column {
            P(
                attrs = Modifier
                    .margin(topBottom = 0.px)
                    .fontFamily(ConstantsObject.FONT_FAMILY)
                    .fontSize(if (breakpoint >= Breakpoint.LG)45.px else 20.px)
                    .fontWeight(FontWeight.Normal)
                    .color(ThemeByKizito.Primary.rgb)
                    .toAttrs()
            ) {
                Text("Hello, I'm")
            }

            P(
                attrs = Modifier
                    .margin(top = 20.px, bottom = 20.px)
                    .fontFamily(ConstantsObject.FONT_FAMILY)
                    .fontSize(if (breakpoint >= Breakpoint.LG)68.px else 40.px)
                    .fontWeight(FontWeight.Bolder)
                    .color(ThemeByKizito.Secondary.rgb)
                    .lineHeight(if (breakpoint >= Breakpoint.LG)70.px else 42.px)
                    .toAttrs()
            ) {
                Text("Kizito Okparanwankwo")
            }


            P(
                attrs = Modifier
                    .margin(top = 10.px, bottom = 5.px)
                    .fontFamily(ConstantsObject.FONT_FAMILY)
                    .fontSize(20.px)
                    .fontWeight(FontWeight.Bolder)
                    .color(ThemeByKizito.Secondary.rgb)
                    .toAttrs()
            ) {
                Text("Mobile & Web Developer")
            }


            P(
                attrs = Modifier
                    .margin(bottom = 25.px)
                    .maxWidth(400.px)
                    .fontFamily(ConstantsObject.FONT_FAMILY)
                    .fontSize(15.px)
                    .fontStyle(FontStyle.Italic)
                    .fontWeight(FontWeight.Normal)
                    .color(ThemeByKizito.Secondary.rgb)
                    .toAttrs()
            ) {
                Text(LOREM_IPSUM_SHORTEST)
            }

            Button (
                attrs = MainButtonStyle.toModifier()
                    .height(40.px)
                    .border(width = 0.px)
                    .borderRadius(r = 5.px)
                    .background(ThemeByKizito.Primary.rgb)
                    .color(Color.white)
                    .cursor(Cursor.Pointer)
                    .toAttrs()
            ){

                Link(
                    modifier = Modifier
                        .color(Color.white)
                        .textDecorationLine(TextDecorationLine.None),
                    text = "Hire me",
                    path = Section.Contact.path
                )

            }



        }

    }

}



@Composable
fun MainImage(){

    Column(modifier = MainImageStyle.toModifier()
        .fillMaxSize(80.percent)
        .fillMaxHeight(),
        verticalArrangement = Arrangement.Bottom
    ) {

        Image(
            modifier = Modifier.fillMaxWidth(),
            src = ResObject.Image.main,
            description = "Main Image"
        )

    }

}
