val AboutImageStyle = CssStyle {
    base {
        Modifier
            /*
            .styleModifier {
                filter(grayscale(100.percent))
            }

             */
            .styleModifier {
            val filters = listOf(
                Filter.list()
            )

            filter(Filter.list()) // Use spread operator
        }
            .styleModifier {
                //filter(Filter.of(grayscale(100.percent)))
                filter(Filter.list()) // Use Filter.list() instead
            }
            .borderRadius(r = 0.px)
            .rotate(0.deg)
            .transition(Transition.of(property = TransitionProperty.All, duration = 200.ms))

    }

    hover{
        Modifier
            .styleModifier {
               // filter(Filter.list(grayscale(1)))
               // grayscale(0.percent)
            }
            .borderRadius(r = 100.px)
            .rotate(2.deg)

    }
}







val AboutTextStyle = CssStyle {
    base {
        Modifier
            .opacity(50.percent)
            .transition(Transition.of(property = "opacity", duration = 200.ms))

    }

    hover{
        Modifier
            .opacity(100.percent)

    }
}



