package com.binkes.kizito_portfolio.components

import androidx.compose.runtime.Composable
import com.binkes.kizito_portfolio.models.ThemeByKizito
import com.binkes.kizito_portfolio.styles.InputStyle
import com.binkes.kizito_portfolio.styles.MainButtonStyle
import com.varabyte.kobweb.compose.css.Cursor
import com.varabyte.kobweb.compose.css.Transition
import com.varabyte.kobweb.compose.css.transition
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.ui.*
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.style.toModifier
import org.jetbrains.compose.web.attributes.InputType
import org.jetbrains.compose.web.css.LineStyle
import org.jetbrains.compose.web.css.border
import org.jetbrains.compose.web.css.ms
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.*


@Composable
fun ContactForm(breakpoint: Breakpoint) {

    Form(
        action = "https://formspree.io/f/mqaakdob",
        attrs = Modifier
            .attrsModifier {
                attr("method", "POST")
            }
            .toAttrs()
    ) {

        Label(
            attrs = Modifier
                .classNames("form-label")
                .toAttrs(),
                    forId = "inputName"
        ) {
            Text("Name")
        }

        Input(
            type = InputType.Text,
            attrs = InputStyle.toModifier()
                .id("inputName")
                .classNames("form-control")
                .width(
                    if (breakpoint >= Breakpoint.MD) 500.px
                else 250.px
                )
                .margin(bottom = 10.px)
                .backgroundColor(ThemeByKizito.LighterGray.rgb)
                .boxShadow(0.px,0.px,0.px,0.px,null)
                .attrsModifier {
                    attr("placeholder", "Full Name")
                    attr("name", "name")
                    attr("required", "true")

                    // Add hover styles directly in CSS
                    attr("onmouseover", "this.style.borderColor='${ThemeByKizito.Primary.rgb}'")
                    attr("onmouseout", "this.style.borderColor='${ThemeByKizito.LightGray.rgb}'")
                }
                .toAttrs()
        )


        Label(
            attrs = Modifier
                .classNames("form-label")
                .toAttrs(),
            forId = "inputEmail"
        ) {
            Text("Email")
        }

        Input(
            type = InputType.Text,
            attrs = Modifier
                .id("inputEmail")
                .classNames("form-control")
                .width(
                    if (breakpoint >= Breakpoint.MD) 500.px
                    else 250.px
                )
                .margin(bottom = 10.px)
                .backgroundColor(ThemeByKizito.LighterGray.rgb)
                .boxShadow(0.px, 0.px, 0.px, 0.px, null)
                .styleModifier {
                    border( width = 2.px, style = LineStyle.Solid, color = ThemeByKizito.LightGray.rgb )
                    transition(Transition.of(property = "border", duration = 200.ms))
                }
                .attrsModifier {
                    attr("placeholder", "Email Address")
                    attr("name", "email")
                    attr("required", "true")

                    // Add hover styles directly in CSS
                    attr("onmouseover", "this.style.borderColor='${ThemeByKizito.Primary.rgb}'")
                    attr("onmouseout", "this.style.borderColor='${ThemeByKizito.LightGray.rgb}'")
                }
                .toAttrs()
        )


        Label(
            attrs = Modifier
                .classNames("form-label")
                .toAttrs(),
            forId = "inputMessage"
        ) {
            Text("Massage")
        }

        TextArea(
            attrs = Modifier
                .id("inputMessage")
                .classNames("form-control")
                .height(150.px)
                .width(
                    if (breakpoint >= Breakpoint.MD) 500.px
                    else 250.px
                )
                .margin(bottom = 20.px)
                .backgroundColor(ThemeByKizito.LighterGray.rgb)
                .boxShadow(0.px,0.px,0.px,0.px,null)
                .styleModifier {
                    border( width = 2.px, style = LineStyle.Solid, color = ThemeByKizito.LightGray.rgb )
                    transition(Transition.of(property = "border", duration = 200.ms))
                }
                .attrsModifier {
                    attr("placeholder", "Your Message")
                    attr("name", "message")
                    attr("required", "true")
                    // Add hover styles directly in CSS
                    attr("onmouseover", "this.style.borderColor='${ThemeByKizito.Primary.rgb}'")
                    attr("onmouseout", "this.style.borderColor='${ThemeByKizito.LightGray.rgb}'")
                }
                .toAttrs()
        )



        Box(
            modifier = Modifier.fillMaxWidth(),
            contentAlignment = Alignment.Center
        ) {
            Button(
                attrs = MainButtonStyle.toModifier()
                    .height(40.px)
                    .border(width = 0.px)
                    .borderRadius(r = 5.px)
                    .backgroundColor(ThemeByKizito.Primary.rgb)
                    .color(Colors.White)
                    .cursor(Cursor.Pointer)
                    .toAttrs()
            ) {
                Text("Submit")
            }
        }


    }

}



